import auth0 from 'auth0-js';
import history from './history';

export default class Auth {
  auth0 = new auth0.WebAuth({
    // domain: 'webapp1.auth0.com',
    // clientID: 'uZxUdMAsiDWeu3OrNpoi4JwJscdF5nAx',
    // redirectUri: 'http://localhost:3000/callback',
    // responseType: 'token id_token',
    // scope: 'openid profile email'
    domain: 'dev-223uudmw.auth0.com',
    clientID: 'Fcp36lRtJz3kRZXAKSYho1NtjB48Bhxk',
    // redirectUri: 'http://35.180.16.198:5010/callback',
    redirectUri: 'http://35.180.16.198/callback',
    // redirectUri: 'http://localhost:3000/callback',
    responseType: 'token id_token',
    scope: 'openid profile email'
  });

  // Request URL: https://webapp1.auth0.com/authorize?client_id=uZxUdMAsiDWeu3OrNpoi4JwJscdF5nAx&response_type=token%20id_token&redirect_uri=http%3A%2F%2Flocalhost%3A3000%2Fcallback&scope=openid%20profile%20email&state=8brI8UOn_VRwLtfMA3iiiAEUbCyzbOKq&nonce=d79Oxs778KnkScvoIxwOjFgiCLuwHCeJ&auth0Client=eyJuYW1lIjoiYXV0aDAuanMiLCJ2ZXJzaW9uIjoiOS4xMi4yIn0%3D

  userProfile = {};

  login = () => {
    this.auth0.authorize();
  };

  handleAuth = () => {
    this.auth0.parseHash((err, authResult) => {
      if (authResult) {
        localStorage.setItem('access_token', authResult.accessToken);
        localStorage.setItem('id_token', authResult.idToken);

        let expiresAt = JSON.stringify(
          authResult.expiresIn * 1000 + new Date().getTime()
        );
        localStorage.setItem('expiresAt', expiresAt);

        this.getProfile();

        setTimeout(() => {
          history.replace('/authcheck');
        }, 2000);
      } else {
        console.log(err);
      }
    });
  };

  getAccessToken = () => {
    if (localStorage.getItem('access_token')) {
      const accessToken = localStorage.getItem('access_token');
      return accessToken;
    } else {
      return null;
    }
  };

  getProfile = () => {
    let accessToken = this.getAccessToken();
    if (accessToken) {
      this.auth0.client.userInfo(accessToken, (err, profile) => {
        if (profile) {
          this.userProfile = { profile };
          // localStorage.setItem('nickname', profile.);
          console.log(profile);
          // localStorage.setItem('profileName', profile.name);
          // localStorage.setItem('profilePicture', profile.picture);
          localStorage.setItem('profileForDb', JSON.stringify(profile));
          // console.log(profile.picture);
        }
      });
    }
  };

  logout = () => {
    localStorage.removeItem('access_token');
    localStorage.removeItem('id_token');
    localStorage.removeItem('expiresAt');
    localStorage.removeItem('profileName');
    localStorage.removeItem('profilePicture');
    localStorage.removeItem('userProfileFromDb');
    localStorage.removeItem('profileForDb');
    setTimeout(() => {
      history.replace('/authcheck');
    }, 200);
  };

  isAuthenticated = () => {
    let expiresAt = JSON.parse(localStorage.getItem('expiresAt'));
    return new Date().getTime() < expiresAt;
  };
}
