import React, { Component } from 'react';
import history from './history';
import * as ACTIONS from '../store/actions/actions';
import { connect } from 'react-redux';
import axios from 'axios';

class AuthCheck extends Component {
  send_profile_to_db = async profile => {
    const data = profile;
    console.log(data);
    // axios.post('/api/post/userprofiletodb', data).then(
    //   axios
    //     .get('/api/get/userprofilefromdb', {
    //       params: { email: profile.profile.email }
    //     })
    //     .then(res => this.props.set_db_profile(res.data))
    // );
    try {
      const userAuthDataToDb = await axios.post(
        '/api/post/userprofiletodb',
        data
      );
      console.log(userAuthDataToDb.status);
    } catch (error) {
      console.log(error);
    }
    try {
      const userFromDB = await axios.get('/api/get/userprofilefromdb', {
        params: { email: profile.profile.email }
      });
      this.props.set_db_profile(userFromDB.data[0]);
      console.log(userFromDB.data[0]);
      localStorage.setItem(
        'userProfileFromDb',
        JSON.stringify(userFromDB.data[0])
      );
    } catch (error) {
      console.log(error);
    }
  };

  componentDidMount() {
    if (this.props.auth.isAuthenticated()) {
      this.props.login_success();
      this.props.add_profile(this.props.auth.userProfile);
      // console.log(JSON.parse(localStorage.getItem('profileForDb')));
      this.send_profile_to_db(this.props.auth.userProfile);
      setTimeout(() => history.replace('/'), 50);
    } else {
      this.props.login_failure();
      this.props.remove_profile();
      this.props.remove_db_profile();
      history.replace('/');
    }
  }

  render() {
    return <div></div>;
  }
}

function mapStateToProps(state) {
  return {};
}

function mapDispatchToProps(dispatch) {
  return {
    login_success: () => dispatch(ACTIONS.login_success()),
    login_failure: () => dispatch(ACTIONS.login_failure()),
    add_profile: profile => dispatch(ACTIONS.add_profile(profile)),
    remove_profile: () => dispatch(ACTIONS.remove_profile()),
    set_db_profile: profile => dispatch(ACTIONS.set_db_profile(profile)),
    remove_db_profile: () => dispatch(ACTIONS.remove_db_profile())
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(AuthCheck);
